@import '../../variables';

.FdeSection {
  // border: 2px solid blue;
  display: flex;
  flex-direction: row;

  width: 100%;
  height: 100%;

  .NorthDepCol,
  .SouthDepCol {
    display: flex;
    flex-direction: column;

    width: 50%;
  }

  .NorthDepCol {
    margin-right: $spacingXs;
    margin-left: $spacingSm;
  }

  .SouthDepCol {
    margin-right: $spacingSm;
    margin-left: $spacingXs;
  }
}
